import React from "react"

export default function BlogPostPreview(data) {
  return (
    <a className="blog-post-preview clickable" href={data.page}>
      <span
        className="warning"
        style={{ visibility: data.inProgress ? `visible` : `hidden` }}
        data-text="This post is under construction"
      />
      <img src={data.img} />
      <div className="blog-post-preview-title">{data.title}</div>
    </a>
  )
}
